import React, { useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {useRoom, useRoomUpdate} from './RoomsContext';

function HomePage() {
    const rm1 = useRoom();
    const fetchdata = useRoomUpdate();

    useEffect(()=> {
        fetchdata();
    });

    return (
        <>
            <div className='HeaderL'>
                <h2 className='mainForm trayLocation'>This page is under developement</h2>
                <h1 className='check'> deployed 0.6 -using context-</h1>
            </div>
            <div>
                {rm1}
            </div>
        </>
    );
};

export default HomePage;

